<template>
  <div class="news-wrap">
    <div id="content">
      <!-- <div class="name">
        <span>资讯</span>
      </div> -->
      <div class="tab">
        <div
          class="tab-item"
          :class="{ 'tab-active': tabActive === 0 }"
          @click="handleTab(0)"
        >
          {{ $t('admin.news') }}
        </div>
        <div
          class="tab-item"
          :class="{ 'tab-active': tabActive === 1 }"
          @click="handleTab(1)"
        >
          {{ $t('admin.article') }}
        </div>
      </div>
      <div class="list">
        <template v-if="newsList.length > 0">
          <NewsItem
            v-for="item in newsList"
            :key="item.id"
            :tabActive="tabActive"
            :dataSource="item"
          ></NewsItem>
        </template>
        <div class="notice" v-else>{{ $t('common.noData') }}</div>
      </div>
    </div>
    <router-view class="child-view"></router-view>
  </div>
</template>

<script>
import NewsItem from './components/NewsItem.vue'
import { handleNewsList } from '@/services/news'
export default {
  components: { NewsItem },
  name: '',
  data () {
    return {
      tabActive: 0,
      newsList: []
    }
  },
  methods: {
    handleTab (key) {
      this.tabActive = key
      this.newsList = []
      this.getList()
    },
    async getList () {
      const resp = await handleNewsList({
        type: this.tabActive,
        orderByColumn: 'id',
        isAsc: 'desc'
      })
      if (!resp.success) return
      this.newsList = resp.rows
    }
  },
  mounted () {
    this.getList()
  }
}
</script>

