<template>
  <div class="news-item-wrap">
    <div class="content">
      <div class="news-content" v-if="tabActive === 0" @click="handleExpand">
        <div class="header">
          <div class="author">
            <img :src="dataSource.logo || defaultImg" alt="" translate="no" />
            <div class="name" translate="no">{{ dataSource.author }}</div>
          </div>
          <div class="time" translate="no">{{ dataSource.updateTime || dataSource.createTime }}</div>
        </div>
        <div
          class="text"
          v-if="!isexpand"
          :class="{ isImportant: dataSource.isImportant }"
        >
          {{ dataSource.content }}
        </div>
        <div class="whole-wrap" v-else>
          <div :class="{ isImportant: dataSource.isImportant }">
            {{ dataSource.content }}
          </div>
          <img :src="dataSource.preview" alt="" />
        </div>
      </div>
      <div
        class="article-content"
        v-else
        @click="$gbUtils.handleToPage('/news/newsDetail?id=' + dataSource.id)"
      >
        <div class="content">
          <div class="img-box">
            <img :src="dataSource.preview" alt="" />
          </div>
          <div class="text">
            <div class="des" :class="{ isImportant: dataSource.isImportant }">
              {{ dataSource.title }}
            </div>
            <div class="info">
              <div class="item">
                <img
                  :src="dataSource.logo || defaultImg"
                  alt=""
                  translate="no"
                />
                <div class="name" translate="no">{{ dataSource.author }}</div>
              </div>
              <div class="item">
                <span translate="no">{{ dataSource.readingVolume }}</span
                >{{ $t('admin.read') }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: '',
  props: ['tabActive', 'dataSource'],
  data () {
    return {
      timeAgo: '',
      isexpand: false,
      defaultImg: require('../../../assets/logo.png')
    }
  },
  methods: {
    handleExpand () {
      this.isexpand = !this.isexpand
    },
    // 计算分钟,小时,天
    getTime (value) {
      if (+value >= 86400) {
        return (+value / 86400).toFixed(0)
      } else if (+value >= 3600) {
        return (+value / 3600).toFixed(0)
      } else if (+value >= 60) {
        return (+value / 60).toFixed(0)
      }
    },
    // 获取天小时单位
    getUnit (value) {
      if (+value >= 86400) {
        return '天前'
      } else if (+value >= 3600) {
        return '小时前'
      } else if (+value >= 60) {
        return '分钟前'
      } else {
        return '秒前'
      }
    }
  },
  mounted () {

  }
}
</script>

